export const fixUrl = (name) => {
  const text = window.location.search.substring(1).split('&');
  let v = null;
  for (let i = text.length - 1; i >= 0; i -= 1) {
    const key = text[i].split('=')[0];
    const value = text[i].split('=')[1];
    if (key === name) {
      v = value;
      break;
    }
  }
  return v;
};
// è¾“å…¥æ¡†
export const fixInput = (val, fixs) => {
  let fix = fixs;
  if (!fix === 0) {
    fix = fix || 10;
  }

  let v = `${fixDEAdd(val, fixs, false)}`;
  // æ“ä½œ1
  // ç”¨æˆ·è¡Œä¸º ç›´æ¥ä¸Šæ¥æ‰“ä¸ª.
  // è§£å†³æ–¹æ¡ˆ ç½®æ¢æˆ0.
  if (v.charAt(0) === '.') {
    v = '0.';
  }
  // æ“ä½œ2
  // ç”¨æˆ·è¡Œä¸º æ‰“å¤šä¸ªç‚¹.
  // è§£å†³æ–¹æ¡ˆ ä¿ç•™ç¬¬äºŒä¸ªç‚¹ä»¥å‰çš„æ•°å€¼
  const strArr = [...v].reduce((res, c) => {
    if (res[c]) {
      res[c] += 1;
    } else {
      res[c] = 1;
    }
    return res;
  }, {});
  if (strArr['.'] === 2) {
    const arr = v.split('.');
    v = `${arr[0]}.${arr[1]}`;
  }
  // æ“ä½œ3
  // ç”¨æˆ·è¡Œä¸º å°æ•°ç‚¹åè¾“å…¥è¶…è¿‡è¯¥å¸ç§ç²¾åº¦é™åˆ¶
  // è§£å†³æ–¹æ¡ˆ ä¿ç•™è¯¥ç²¾åº¦ä¹‹å‰çš„æ•°å€¼
  if (v.indexOf('.') !== -1) {
    const integer = v.split('.')[0]; // æ•´æ•°
    let decimal = v.split('.')[1]; // å°æ•°
    if (decimal.length > fix) {
      decimal = decimal.substring(0, fix);
      v = `${integer}.${decimal}`;
    }
  }
  // æ“ä½œ4
  // ç”¨æˆ·è¡Œä¸º è½¬æˆæ±‰è¯­æ‹¼éŸ³åå¯è¾“å…¥æ±‰å­—å­—æ¯ç­‰å­—ç¬¦
  // è§£å†³æ–¹æ¡ˆ å¹²æ‰å†™å…¥çš„æ–‡å­—
  const strKey = Object.keys(strArr);
  strKey.forEach((c) => {
    let str = '01234567890.';
    if (fix === 0) { str = '01234567890'; }
    if (str.indexOf(c) === -1) {
      v = v.split(c)[0] + (v.split(c)[1] || '');
    }
  });
  // æ“ä½œ5
  // ç”¨æˆ·è¡Œä¸º è¾“å…¥æ€»é•¿åº¦è¶…è¿‡14ä½ åŒ…æ‹¬.
  // è§£å†³æ–¹æ¡ˆ æˆªå–å‰14ä½
  if (v.length > 13) {
    v = v.substring(0, 13);
  }
  return v;
};
// è®¡ç®—å€’è®¡æ—¶ ä»…æ”¯æŒåˆ°å¤©ä½
export const getTime = (spkTime) => {
  let dateSpan = Math.abs(spkTime);
  if (!dateSpan) { return ''; }
  // è®¡ç®—å¤©
  const iDays = Math.floor(dateSpan / (24 * 3600 * 1000));
  // è®¡ç®—å°æ—¶
  dateSpan -= (iDays * (24 * 3600 * 1000));
  let iHour = `${Math.floor(dateSpan / (3600 * 1000))}`;
  iHour = iHour.length === 1 ? `0${iHour}` : iHour;
  // è®¡ç®—åˆ†é’Ÿ
  dateSpan -= (iHour * (3600 * 1000));
  let iMin = `${Math.floor(dateSpan / (60 * 1000))}`;
  iMin = iMin.length === 1 ? `0${iMin}` : iMin;
  // è®¡ç®—ç§’é’Ÿ
  dateSpan -= (iMin * (60 * 1000));
  let iSec = `${Math.floor(dateSpan / 1000)}`;
  iSec = iSec.length === 1 ? `0${iSec}` : iSec;
  return {
    day: iDays,
    hour: iHour,
    min: iMin,
    sec: iSec,
  };
};
// è®¡ç®—æ—¶é—´ 2012:12:12 12:12:12
export function timeFn(dataObj, data) {
  let fmt = data;
  const o = {
    'M+': dataObj.getMonth() + 1, // æœˆä»½
    'd+': dataObj.getDate(), // æ—¥
    'h+': dataObj.getHours(), // å°æ—¶
    'm+': dataObj.getMinutes(), // åˆ†
    's+': dataObj.getSeconds(), // ç§’
    'q+': Math.floor((dataObj.getMonth() + 3) / 3), // å­£åº¦
    S: dataObj.getMilliseconds(), // æ¯«ç§’
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (`${dataObj.getFullYear()}`).substr(4 - RegExp.$1.length));
  }
  Object.keys(o).forEach((k) => {
    if (new RegExp(`(${k})`).test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k])
        : ((`00${o[k]}`).substr((`${o[k]}`).length)));
    }
  });
  return fmt;
}
export const getComplexType = (obj) => {
  const type = Object.prototype.toString.call(obj);
  return type.match(/\s(.+)]/ig)[0].trim().replace(']', '');
};

export const thousands = (num) => {
  const str = num.toString();
  const reg = str.indexOf('.') > -1 ? /(\d)(?=(\d{3})+\.)/g : /(\d)(?=(?:\d{3})+$)/g;
  return str.replace(reg, '$1,');
};


export function getLocationLang() {
  const langList = ['el_GR', 'en_US', 'ja_JP', 'ko_KR', 'vi_VN', 'zh_CN'];
  const hrefLang = window.location.href.match(/lang=(.+?)(&|$)/);
  let url = '';

  if (hrefLang && hrefLang[1] && langList.indexOf(hrefLang[1]) > -1) {
    [, url] = hrefLang;
  }

  return url;
}

export function getCoinShowName(name, coinList = {}) {
  if (name && coinList && coinList[name.toUpperCase()]) {
    return coinList[name.toUpperCase()].showName || name;
  }
  return name;
}
export function fixFloat(num, precision) {
  const fix = Number(precision);
  const n = `${num}`;
  const arr = n.split('.');
  if (Number.isNaN(Number(num))) {
    return '--';
  }
  if (!fix || arr.length === 1) {
    return arr[0];
  }
  return `${arr[0]}.${arr[1].substring(0, fix)}`;
}
// åƒä½æ•°å­—æ¯ä¸‰ä½åŠ é€—å·
export const thousandsComma = (num) => {
  const Num = (num || 0).toString();
  let number = 0;
  let floatNum = '';
  let intNum = '';
  // åˆ¤æ–­æ˜¯å¦æœ‰å°æ•°ä½ï¼Œæœ‰åˆ™æˆªå–å°æ•°ç‚¹åçš„æ•°å­—
  if (Num.indexOf('.') > 0) {
    number = Num.indexOf('.'); // è·å–å°æ•°ç‚¹å‡ºç°çš„ä½ç½®
    floatNum = Num.substr(number); // æˆªå–arr.substr(form, length)
    intNum = Num.substring(0, number); // æˆªå–arr.substring(start, end)
  } else {
    intNum = Num;
  }
  const result = [];
  let counter = 0;
  intNum = intNum.split('');
  // åˆ©ç”¨3çš„å€æ•°ï¼Œå‘æ•°ç»„æ’å…¥','
  for (let i = intNum.length - 1; i >= 0; i -= 1) {
    counter += 1;
    result.unshift(intNum[i]);
    if (!(counter % 3) && i !== 0) { result.unshift(','); }
  }
  return result.join('') + floatNum || '';
};
