import React, { Component } from 'react';
import  Home  from "./Home"

class Page extends Component {
    constructor(props) {
        super(props)
        this.state = {page:'',id:0 }
    }
    componentDidMount(){
        let search = this.props.location.pathname.replace(/\//,'').split('&');
        console.log(search)
        let page = search[0]
        this.setState({page:page})
    }
    UNSAFE_componentWillReceiveProps(nextProps){
        if(nextProps!==this.props){
            this.setState({nextProps})
            let search = nextProps.location.pathname.replace(/\//,'').split('&');
            let page = search[0] === '' ?  'Home' : search[0]
            this.setState({page:page})
            return true
        }else{
            return false
        }
    }
    render() { 
        switch (this.state.page){
            case 'Home':
                return(<Home></Home>)
            default:
                return(<Home></Home>)
        }
    }
}
 
export default Page;